import React from "react";

const DisplayFormResult = ({ data }) => {
  console.log(data);
  return (
    <div className="card-list">
      {data
        .filter((film) => film.poster_path)
        .map((film) => (
          <div className="card" key={film.id}>
            <div className="card-img">
              <img
                className="card--image"
                src={`https://image.tmdb.org/t/p/w185_and_h278_bestv2/${film.poster_path}`}
                alt={film.title + "poster"}
              />
              <h3 className="card--title-a">{film.title}</h3>
            </div>
            <div className="card--content">
              <h3 className="card--title-b">{film.title}</h3>
              <p>
                <small>RELEASE DATE: {film.release_date}</small>
              </p>
              <p>
                <small>RATING: {film.vote_average}</small>
              </p>
              <p className="card--desc">{film.overview}</p>
            </div>
          </div>
        ))}
    </div>
  );
};

export default DisplayFormResult;
