import React, { useState } from "react";
import SearchMovies from "./search";
import DisplayFormResult from "./displayFormRes";
import axios from "axios";

const App = () => {
  const [query, setQuery] = useState("");
  const [data, setData] = useState([]);
  const searchMovie = async (e) => {
    e.preventDefault();
    const url = `https://api.themoviedb.org/3/search/movie?api_key=f3685a807402a1b4d8faa777f3e03d3e&language=eng-US&query=${query}&include_adult=false`;
    return await axios.get(`${url}`).then((res) => {
      if (res.status === 200) {
        setData(res.data.results);
      }
    });
  };
  const handleChange = (item) => {
    let newValue = item.target.value;
    setQuery(newValue);
  };

  return (
    <div className="container">
      <h2 className="title">Netflix Pro </h2>
      <SearchMovies
        searchMovie={searchMovie}
        query={query}
        handleChange={handleChange}
      />
      <DisplayFormResult data={data} />
    </div>
  );
};

export default App;
